    position: fixed;
    bottom: 59px;
    right: 20px;
    z-index: 1000;
    cursor: pointer;
  }
  /* Style for chatbot icon */
.chatbot-icon {
    width: 100px!important;
    height: 100px!important;
    background: url('http://product-service-fe.dv/dist/assets/images/chat-bot.svg') center/cover no-repeat; /* Replace 'chatbot-icon.png' with your icon image */
}

.chatbot-toggler {
    width: 60px;
    height: 60px;
    display: flex;
    cursor: pointer;
    align-items: center;
    justify-content: center;
    outline: none;
    border: none;
    border-radius: 50%;
    background: #ffcb08 url('http://product-service-fe.dv/dist/assets/images/chat-bot.svg') no-repeat;
    background-size: 100%;
    background-position: center center;
    position: fixed;
    bottom: 0px;
    right: 35px;
    padding: 8px;
    -webkit-animation: sk-bounce 2.0s infinite ease-in-out;
    animation: sk-bounce 2.0s infinite ease-in-out;
}

.chatbot {
    width: 100%;
    max-width: 420px;
    background: #fff;
    border-radius: 15px;
    pointer-events: none;
    /* box-shadow: 0 0 128px 0 rgba(0, 0, 0, 0.1), 0 32px 64px -48px rgba(0, 0, 0, 0.5); */
    overflow: hidden;
    opacity: 0;
    position: fixed;
    right: 35px;
    bottom: 90px;
    top: 0;
    transform: scale(0);
    transform-origin: bottom right;
    transition: all 0.3s ease;
}

.chatbot-toggler span {
    font-size: 0;
    position: absolute;
}

.chatbot-toggler span:last-child {
    opacity: 0;
}

.show-chatbot .chatbot {
    opacity: 1;
    pointer-events: auto;
    transform: scale(1);
}

.show-chatbot .chatbot-toggler {
    background-image: url('http://product-service-fe.dv/dist/assets/images/close-icon.svg');
    background-size: 50%;
}

.show-chatbot .chatbot-toggler span:first-child {
    opacity: 0;
}

.show-chatbot .chatbot-toggler span:last-child {
    opacity: 1;
}

.chatbot {
    width: 100%;
    max-width: 420px;
    background: #fff;
    border-radius: 15px;
    pointer-events: none;
    /* box-shadow: 0 0 128px 0 rgba(0, 0, 0, 0.1), 0 32px 64px -48px rgba(0, 0, 0, 0.5); */
    overflow: hidden;
    opacity: 0;
    position: fixed;
    right: 35px;
    bottom: 90px;
    top: 0;
    transform: scale(0);
    transform-origin: bottom right;
    transition: all 0.3s ease;
}


@keyframes sk-bounce {
    0%, 100% {
      transform: scale(0.8);
    } 50% {
        transform: scale(1.0);
      }
}`, "",{"version":3,"sources":["webpack://./public/widget/plugin/common.css"],"names":[],"mappings":"AAAA;IACI,eAAe;IACf,YAAY;IACZ,WAAW;IACX,aAAa;IACb,eAAe;EACjB;EACA,2BAA2B;AAC7B;IACI,sBAAsB;IACtB,uBAAuB;IACvB,sGAAsG,EAAE,oDAAoD;AAChK;;AAEA;IACI,WAAW;IACX,YAAY;IACZ,aAAa;IACb,eAAe;IACf,mBAAmB;IACnB,uBAAuB;IACvB,aAAa;IACb,YAAY;IACZ,kBAAkB;IAClB,iGAAiG;IACjG,qBAAqB;IACrB,kCAAkC;IAClC,eAAe;IACf,WAAW;IACX,WAAW;IACX,YAAY;IACZ,sDAAsD;IACtD,8CAA8C;AAClD;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;IACnB,oBAAoB;IACpB,sFAAsF;IACtF,gBAAgB;IAChB,UAAU;IACV,eAAe;IACf,WAAW;IACX,YAAY;IACZ,MAAM;IACN,mBAAmB;IACnB,8BAA8B;IAC9B,yBAAyB;AAC7B;;AAEA;IACI,YAAY;IACZ,kBAAkB;AACtB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,UAAU;IACV,oBAAoB;IACpB,mBAAmB;AACvB;;AAEA;IACI,uFAAuF;IACvF,oBAAoB;AACxB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,WAAW;IACX,gBAAgB;IAChB,gBAAgB;IAChB,mBAAmB;IACnB,oBAAoB;IACpB,sFAAsF;IACtF,gBAAgB;IAChB,UAAU;IACV,eAAe;IACf,WAAW;IACX,YAAY;IACZ,MAAM;IACN,mBAAmB;IACnB,8BAA8B;IAC9B,yBAAyB;AAC7B;;;AAGA;IACI;MACE,qBAAqB;IACvB,EAAE;QACE,qBAAqB;MACvB;AACN","sourcesContent":["#chatbot-drag {\n    position: fixed;\n    bottom: 59px;\n    right: 20px;\n    z-index: 1000;\n    cursor: pointer;\n  }\n  /* Style for chatbot icon */\n.chatbot-icon {\n    width: 100px!important;\n    height: 100px!important;\n    background: url('http://product-service-fe.dv/dist/assets/images/chat-bot.svg') center/cover no-repeat; /* Replace 'chatbot-icon.png' with your icon image */\n}\n\n.chatbot-toggler {\n    width: 60px;\n    height: 60px;\n    display: flex;\n    cursor: pointer;\n    align-items: center;\n    justify-content: center;\n    outline: none;\n    border: none;\n    border-radius: 50%;\n    background: #ffcb08 url('http://product-service-fe.dv/dist/assets/images/chat-bot.svg') no-repeat;\n    background-size: 100%;\n    background-position: center center;\n    position: fixed;\n    bottom: 0px;\n    right: 35px;\n    padding: 8px;\n    -webkit-animation: sk-bounce 2.0s infinite